import React, { useState, useEffect } from 'react'
import { Table, Button, message, Modal, Card, Space, Input, Row, Col, Tag } from 'antd';
//helpers
import Api from '../../helpers/api';
import handleApiError from '../../helpers/handleApiError';
import hasAuthObject from '../../helpers/hasAuthObject';
import {
  PlusOutlined,
  EditOutlined,
  DeleteOutlined,
  EyeOutlined
} from '@ant-design/icons';

const api = new Api();

// setup auth object config for this page
const authobjectCfg = {
  view: 'material-view', //button display detail
  create: 'material-create', //button add
  update: 'material-update', //button edit
  delete: 'material-delete', //button delete
}

const List = (props) => {

  const [selectedRowKeys, setSelectedRowKeys] = useState([])
  const [q, setQ] = useState('')
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState([])
  const [pagination, setPagination] = useState({})

  useEffect(() => {
    fetch()
  }, [q])

  const fetch = (params = {}) => {
    // console.log('params:', params);
    setLoading(true)

    api.getMasterMaterialList({ ...params, q })
      .then(response => {
        const data = response.data;

        let pagination = {}

        pagination.current = data.current_page
        pagination.total = data.total;
        pagination.pageSize = parseInt(data.per_page);

        setData(data.data)
        setPagination(pagination)
        setLoading(false)
      })
      .catch(error => {
        setLoading(false)
        handleApiError(error, props)
      })

  }

  const handleTableChange = (paginate, filters, sorter) => {

    const pager = { ...pagination };
    pager.current = paginate.current;

    setPagination(pager)

    fetch({
      per_page: paginate.pageSize,
      page: paginate.current,
      sort_field: sorter.field,
      sort_order: sorter.order === 'ascend' ? 'asc' : 'desc',
      // ...filters,
    });
  }

  const handleEdit = () => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);

    if (selectedRowKeys.length > 1) {
      Modal.warning({
        title: `Anda memilih ${selectedRowKeys.length} data, silahkan hanya pilih 1 data`,
      });
    } else {
      props.history.push(`/material/edit/${selectedRowKeys[0]}`)
    }
  }

  const handleView = () => {
    if (selectedRowKeys.length > 1) {
      Modal.warning({
        title: `Anda memilih ${selectedRowKeys.length} data, silahkan hanya pilih 1 data`,
      });
    } else {
      props.history.push(`/material/view/${selectedRowKeys[0]}`)
    }
  }

  const handleDelete = () => {
    Modal.confirm({
      title: `Delete ${selectedRowKeys.length} data?`,
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      onOk: () => {
        massDeleteData()
      },
      onCancel: () => {
        console.log('Cancel');
      },
    });
  }

  const massDeleteData = () => {
    api.massDeleteMaterial({ id: selectedRowKeys })
      .then(response => {
        // console.log(response.data)
        message.success('Delete data success');
        fetch();
        setSelectedRowKeys([])
      })
      .catch(error => {
        //console.log(error.response)
        message.error('Failed to delete');
        handleApiError(error, props)
      });
  }

  //on check table item
  const onSelectChange = (selectedRowKeys) => {
    // console.log('selectedRowKeys changed: ', selectedRowKeys);
    setSelectedRowKeys(selectedRowKeys)
  }

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
    // type: 'radio'
  };

  const hasSelected = selectedRowKeys.length > 0;

  const searchData = (value) => {
    setQ(value);
  }

  const columns = [
    {
      title: 'Name',
      dataIndex: 'material_code',
      sorter: true,
    },
    {
      title: 'Description',
      dataIndex: 'description',
      sorter: true,
    },
    {
      title: 'Classification',
      dataIndex: 'classification',
      render: (classification) => {
        if (classification) { return classification.name }
      },
    },
    {
      title: 'Unit of Measurement (UoM)',
      dataIndex: 'uom',
      render: (uom) => {
        if (uom) { return uom.name }
      },
    },
    // {
    //   title: 'Require Serial Number?',
    //   dataIndex: 'serial_number',
    //   render: (serial_number) => (<Tag style={{ marginBottom: 3, marginTop: 3 }}>{serial_number ? 'Yes' : 'No'}</Tag>),
    //   sorter: true,
    // },
  ]

  return (
    <Card>
      <Space direction="vertical" size="large" style={{ width: '100%' }}>
        <Row>
          <Col md={19}>
            <Space>
              {hasAuthObject(authobjectCfg.create) &&
                <Button
                  type="primary"
                  onClick={() => { props.history.push('/material/add') }}
                  icon={<PlusOutlined />}
                >Add</Button>
              }

              {hasAuthObject(authobjectCfg.update) &&
                <Button
                  onClick={() => handleEdit()}
                  disabled={!hasSelected}
                  icon={<EditOutlined />}
                >Edit</Button>
              }

              {hasAuthObject(authobjectCfg.view) &&
                <Button
                  onClick={() => handleView()}
                  disabled={!hasSelected}
                  icon={<EyeOutlined />}
                >View</Button>
              }

              {hasAuthObject(authobjectCfg.delete) &&
                <Button
                  danger
                  onClick={() => handleDelete()}
                  disabled={!hasSelected}
                  icon={<DeleteOutlined />}
                >Delete</Button>
              }
            </Space>
          </Col>

          <Col md={5} >
            <Input.Search placeholder="Search"
              onSearch={searchData} />
          </Col>
        </Row>
        <Row>
          <Col lg={24} xs={24}>
            <Table
              loading={loading}
              rowSelection={hasAuthObject(authobjectCfg.view || authobjectCfg.update || authobjectCfg.delete) ? rowSelection : null}
              columns={columns}
              rowKey={record => record.id}
              onChange={handleTableChange}
              dataSource={data}
              pagination={{
                pageSizeOptions: ['20', '50', '100', '200'],
                showSizeChanger: true,
                showQuickJumper: true,
                showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
                ...pagination
              }}
              scroll={{ y: 300, x: 1000 }} />
          </Col>
        </Row>
      </Space>
    </Card>
  )
}

export default List